.masonry {
  transition: all .5s ease-in-out;
  column-gap: 30px;
  column-fill: initial;
}

.masonry .brick {
  margin-bottom: 30px;
  overflow: hidden;
}

.masonry .brick img {
  transition: all .5s ease-in-out;
}

.masonry .brick:hover img {
  opacity: .75;
}

.masonry.bordered {
  column-rule: 1px solid #eee;
  column-gap: 50px;
}

.masonry.bordered .brick {
  padding-bottom: 25px;
  margin-bottom: 25px;
  border-bottom: 1px solid #eee;
}

.masonry.gutterless {
  column-gap: 0;
}

.masonry.gutterless .brick {
  margin-bottom: 0;
}

.span {
  column-span: all;
  *margin: 30px 0;
}

@media only screen and (min-width: 1024px) {
  .desc {
    font-size: 1.25em;
  }
  .intro {
    letter-spacing: 1px;
  }
  .wrapper {
    width: 80%;
    padding: 2em;
  }
  .masonry {
    column-count: 3;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .wrapper {
    width: 85%;
    padding: 1.5em;
  }
  .masonry {
    column-count: 2;
  }
}

